<script>
import { getWallet } from '@/http/service/mine'

export default {
  data() {
    return {
      walletInfo: {}
    }
  },
  activated() {
    /**
     * @description 获取钱包信息
    */
    getWallet().then(res => {
      // console.log(res)
      this.walletInfo = res.data.data
    })
  },
  methods: {
    toEcard() {
      this.$router.push('/ecardlist')
    },
    /**
     * @description 跳转我的资产
    */
    toWallet() {
      this.$router.push('/wallet')
    },
    /**
     * @description 点击跳转到优惠券部分
    */
    toCoupon() {
      const context = this;
      context.$router.push({
        path: '/coupon',
      });
    },
    toZuanshi() {
      this.$router.push('/diamond')
    }
  },
}
