<template>
  <div class="me-wallet">
    <div class="me-wallet-sing">
      <span class="me-wallet-ready">我的订单</span>
      <span class="me-wallet-more" @click="toOrderList(0)">查看订单<van-icon name="arrow" /></span>
    </div>
    <div
      class="me-wallte-ground"
      :model="orderCount">
      <ul>
        <li @click="toOrderList(1)">
          <div class="me-wallte-num">
            <strong class="me-wallte-numTips" v-if="Number(orderCount.waitPay) > 0">{{orderCount.waitPay}}</strong>
            <p class="white-str white-outstanding"></p>
          </div>
          <span class="me-wallte-account">待付款</span>
        </li>
        <li @click="toOrderList(2)">
          <div class="me-wallte-num">
            <strong class="me-wallte-numTips" v-if="Number(orderCount.waitShipping) > 0">{{orderCount.waitShipping}}</strong>
            <p class="white-str white-shipment"></p>
            <!-- <img src="@/assets/imgs/usercenter/user-shipment.png" alt="" class="white-str"> -->
          </div>
          <span class="me-wallte-account">待发货</span>
        </li>
        <li @click="toOrderList(3)">
          <div class="me-wallte-num">
            <strong class="me-wallte-numTips" v-if="Number(orderCount.waitReceipt) > 0">{{orderCount.waitReceipt}}</strong>
            <p class="white-str white-collection"></p>
            <!-- <img src="@/assets/imgs/usercenter/user-maintenance.png" alt="" class="white-str"> -->
          </div>
          <span class="me-wallte-account">待收货</span>
        </li>
        <li @click="toOrderList(4)">
          <div class="me-wallte-num">
            <strong class="me-wallte-numTips" v-if="Number(orderCount.waitComment) > 0">{{orderCount.waitComment}}</strong>
            <p class="white-str white-appraisals"></p>
            <!-- <img src="@/assets/imgs/usercenter/user-appraisals.png" alt="" class="white-str"> -->
          </div>
          <span class="me-wallte-account">待评价</span>
        </li>
        <li @click="toRefundRepair">
          <div class="me-wallte-num">
            <strong class="me-wallte-numTips" v-if="Number(orderCount.rightService) > 0">{{orderCount.rightService}}</strong>
            <p class="white-str white-maintenance"></p>
            <!-- <img src="@/assets/imgs/usercenter/user-maintenance.png" alt="" class="white-str"> -->
          </div>
          <span class="me-wallte-account">退款/售后</span>
        </li>
      </ul>
    </div>
  </div>
</template>
<script>
import { getOrderCount } from '@/http/service/mine'
export default {
  data() {
    return {
      orderCount: []
    }
  },
  methods: {
    /**
     * @description  点击跳转到订单列表页面
    */
    toOrderList (tagChecked) {
      const context = this;
      context.$router.push({
        path: `/orderList?tagChecked=${tagChecked}`,
      });
    },

    /**
     * @description 点击跳转到售后列表页面
    */
    toRefundRepair () {
      const context = this;
      context.$router.push({
        path: '/repair'
      });
    }
  },
  mounted() {
    getOrderCount().then(res => {
      // console.log(res, '数量')
      this.orderCount = res.data.data
    })
  }
}
</script>
