<template>
  <div class="mine">
    <!-- 账户信息 -->
    <userInfo />
    <div class="list">
      <!-- 我的钱包 -->
      <userWallet />
      <!-- 我的订单 -->
      <userOrder />
      <!-- 我的店铺 -->
      <!-- <userShops v-if="0"/> -->
      <!-- 常用功能 -->
      <!-- <userCommon v-if="0"/> -->
      <!--其他入口列表-->
      <sectionCard v-if="displayStoreModule" :cdnImageUrl="$cdnImageUrl" :info="store"/>
      <sectionCard :info="displayMy" :cdnImageUrl="$cdnImageUrl" />
      <!-- <userList></userList> -->
      <otherLink></otherLink>
    </div>
  </div>
</template>

<script>
import userInfo from './components/user-info'
import userWallet from './components/user-wallet'
import userOrder from './components/user-order'
// import userShops from './components/user-shops'
// import userCommon from './components/user-common'
// import userList from './components/user-list'
import otherLink from './components/other-link'
import sectionCard from './components/sectionCard'
import { mapGetters } from 'vuex'
import { getUcLoginUrl } from '@/http/service/common.js'
import userKeyTarget from '@/utils/userTarget'

export default {
  components: {
    userInfo,
    userWallet,
    userOrder,
    sectionCard,
    // userShops,
    // userCommon,
    // userList,
    otherLink
  },
  name: 'mine',
  computed: {
    ...mapGetters('mymanage', ['displaySetting', 'displayStoreEnter', 'displayStoreModule', 'getIdentify']),
    displayMy() {
      return {
        title: '我的功能',
        items: this.my.items.filter(v => {
          return !v.permission || v.permission()
        })
      }
    }
  },
  activated() {
    this.$store.dispatch('mymanage/fetchManageInfo')
  },
  data() {
    return {
      store: {
        title: '我的店铺',
        items: [
          {
            icon: '/mine/icon/salary.png',
            title: '我的营收',
            func: () => {
              this.$router.push({
                path: '/financialStation',
              });
            },
          },
          {
            icon: '/mine/icon/otherorder.png',
            title: '用户订单',
            func: () => {
              this.$router.push({
                path: '/orderlist',
                query: {
                  otherOrder: 1
                }
              });
            },
          },
        ]
      },
      my: {
        title: '我的功能',
        items: [
          {
            icon: '/mine/icon/address.png',
            title: '地址管理',
            func: () => {
              this.$router.push({
                path: '/address',
              });
            },
          },
          {
            icon: '/mine/icon/collection.png',
            title: '我的收藏',
            func: () => {
              this.$router.push({
                path: '/myCollection',
              });
            },
          },
          {
            icon: '/mine/icon/gift.png',
            title: '我的奖品',
            func: () => {
              // 跳转逻辑copy自 ./components/user-list.vue
              getUcLoginUrl({
                redirectUri: 'https://hzy.haier.com/cn/vipcode/prize?source=shunguang',
                target: userKeyTarget.HZYTarget,
                targetTest: userKeyTarget.HZYTarget_test,
                redirectUriTest: 'https://hzytest.haier.com/cn/vipcode/prize?source=shunguang',
              }).then(({ data }) => {
                if (data.success) {
                  location.href = data.data
                } else {
                  this.$toast(data.message)
                }
              })
            },
          },
          {
            icon: '/mine/icon/reserve.png',
            title: '我的预约',
            func: () => {
              this.$router.push({
                path: '/myReserve',
              });
            },
          },
          {
            icon: '/mine/icon/cs.png',
            title: '客服中心',
            func: () => {
              // 跳转逻辑copy自 ./components/user-list.vue
              // 如果是海尔app隐藏客服头部栏，使用app内头部栏，否则使用客服工具头部栏
              this.$store.dispatch('goPageByType', {
                type: 29,
                params: {
                  sceneId: 'cj092'
                }
              })
            },
          },
          {
            icon: '/mine/icon/help.png',
            title: '帮助中心',
            func: () => {
              this.$router.push({
                path: '/help',
              });
            },
          },
          {
            icon: '/mine/icon/businessCollege.png',
            title: '商学院',
            permission: () => {
              return !this.$util.isUplus() && this.getIdentify
            },
            func: () => {
              this.$router.push({
                path: '/documentInfo',
                query: {
                  id: 1499,
                  title: '商学院'
                }
              });
            },
          }
        ]
      },
    }
  }
}
</script>

<style scoped lang='scss'>
.mine {
  height: 100%;
  overflow: hidden;
  overflow-y: auto;
  background-color: #f5f5f5;
}
.list{
  position: relative;
  top: -0.8rem;
  padding: 0 0.24rem;
  div{
    border-radius: 0.24rem;
    padding: 0.24rem;
  }
}
